@import url(//fonts.googleapis.com/css?family=Muli);
.head{
     font-family: "Muli",sans-serif;
     font-weight: 700;
     line-height: 1.2;

}
body {
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}
#hov:hover {
  color: black;
}
.Nav_brand {
  text-decoration: none;
  color: black;
}
.menu_active {
  border-bottom: solid 2px black;
}
.pt-5 {
  padding-top: 7rem !important;
}
.animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
.container_service{
  height: 100%;
}
.footer {
  font-size: 12px;
}
.containerr {
  background-color:white;
  padding-left: 10px;
  text-align: center;
  margin-top: 20px;
  
}
.nav_brand {
  font-size: 30px;
}
.cardteam{
    background: #fff;
    border: medium none;
    border-radius: 3px;
    transition:0.3s;
    padding-top: 50px;
}
.cardteam:hover{
    box-shadow: 0 0 20px 0 rgba(0,0,0,0.3);
    transform: translateY(-20px);
}


.container_services{

        height: 75vh;
}
.maincontainer{
  height: 80vh;
}
.services{
  background: #fff;
  border: medium none;
  border-radius: 3px;
  transition:0.3s;
  
}
.services:hover{
  box-shadow: 0 0 20px 0 rgba(0,0,0,0.3);
  transform: translateY(-20px);
}

.headerr{
  width: 100%;
  height: 100vh;
}
@media (max-width: 768px){
.head {
    font-size: 28px;
    font-weight: 700;
}

}

